import React from 'react';
import ReactTable from 'react-table';

// our imports
import './sales.css';
import Header from '../../../components/header/Header';
import SearchBar from '../../../components/searchBar/SearchBar'
import Card from '../../../components/card/ProductCard'

import Unicornio from "../../../img/unicornio.jfif";
import { Link } from 'react-router-dom/cjs/react-router-dom.min';

const productList = [
  {name: 'Ovo de unicornio da angola', value: 4200.00, qtd:25, image: Unicornio},
  {name: 'Ovo de ET', value: 3600.00, qtd:3, image: Unicornio},
  {name: 'Ovo de Savio', value: 1000.00, qtd: 15, image: Unicornio}  
];

const columns = [
  {Header: 'Nome', accessor: 'name'},
  {Header: 'Quantidade',accessor: 'qtd'},
  {Header: 'Valor Unitário',accessor: 'value'}
]

var totalValue = 0;

const Sales = () => {
    return(
        <div>
          <Header />

          <ReactTable
            data={productList}
            columns={columns}
            defaultPageSize={2}
            pageSizeOptions={[2,4,8]}
          />

        </div>
    );
}
export default Sales;